import React from 'react';
import Typography from '@material-ui/core/Typography';

import BaseCard from '../BaseCard'
import src from './image.jpg'
import propTypes, { defaultProps } from './propTypes';

const title  = 'Clean up';

const CleanupCard = () => (
  <BaseCard title={title} image={{ title, src }}>
    <Typography>
      House, shed, yard and garden clean up.
    </Typography>
  </BaseCard>
);

CleanupCard.propTypes = propTypes;
CleanupCard.defaultProps = defaultProps;

export default CleanupCard;
