import React from 'react';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';

import BaseCard from '../BaseCard'
import src from './image.jpg'
import tick from './tick.png'
import propTypes, { defaultProps } from './propTypes';
import styles from './styles';

const title  = 'Removal';

const ListItem = ({ children }) => <li><Typography>{children}</Typography></li>

const RemovalCard = ({ classes }) => (
  <BaseCard title={title} image={{ title, src }}>
    <ul className={classes.list} style={{listStyleImage: 'url(' + tick + ')'}}>
      <ListItem>Concrete</ListItem>
      <ListItem>Brick</ListItem>
      <ListItem>Rock</ListItem>
      <ListItem>Soil</ListItem>
      <ListItem>Green/Garden waste</ListItem>
    </ul>
  </BaseCard>
);

RemovalCard.propTypes = propTypes;
RemovalCard.defaultProps = defaultProps;

export default withStyles(styles)(RemovalCard);
