import React from 'react';
import Grid from '@material-ui/core/Grid';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import PinIcon from '@material-ui/icons/PinDrop';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';

import propTypes, { defaultProps } from './propTypes';
import styles from './styles';

import src from './image.png'

const LocationListItem = ({ text, className }) => (
  <ListItem disableGutters className={className}>
    <ListItemIcon>
      <PinIcon />
    </ListItemIcon>
    <ListItemText primary={text} />
  </ListItem>
)

const ContactSection = ({ classes }) => (
  <div className={classes.root}>
    <Typography variant="h2" color="inherit" className={classes.heading}>
      Contact
    </Typography>
    <Grid container direction="row" justify="center" spacing={16}>
      <Grid item xs={12} sm={6}>
        <div className={classes.contact}>
          <Typography variant="h6" color="inherit">
            0408 242 015
          </Typography>
          <Typography variant="h6" color="inherit" >
            andrew@scrr.com.au
          </Typography>
        </div>
        <div className={classes.area}>
          <Typography variant="h6" color="inherit" >
            We service Melbourne's eastern suburbs including
          </Typography>
          <List>
            <LocationListItem className={classes.item} text="Bayswater" />
            <LocationListItem className={classes.item} text="Boronia" />
            <LocationListItem className={classes.item} text="Chirnside Park" />
            <LocationListItem className={classes.item} text="Croydon" />
            <LocationListItem className={classes.item} text="Ferntree Gully" />
            <LocationListItem className={classes.item} text="Kilsyth" />
            <LocationListItem className={classes.item} text="Lilydale" />
            <LocationListItem className={classes.item} text="Mooroolbark" />
            <LocationListItem className={classes.item} text="Montrose" />
            <LocationListItem className={classes.item} text="Ringwood" />
            <LocationListItem className={classes.item} text="Wantirna" />
          </List>
        </div>
      </Grid >
      <Grid item xs={12} sm={6}>
        <img src={src} alt="map" className={classes.map}/>
      </Grid>
    </Grid>
  </div>
);

ContactSection.propTypes = propTypes;
ContactSection.defaultProps = defaultProps;

export default withStyles(styles)(ContactSection);
