export default theme => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    margin: theme.spacing.unit,
  },
  heading: {
    fontSize: '2.5rem',
    margin: theme.spacing.unit * 2,
  },
  contact: {
    display: 'flex',
    flexDirection: 'column',
  },
  area: {
    marginTop: theme.spacing.unit * 2,
  },
  item: {
    paddingTop: 5,
    paddingBottom: 5,
  },
  map: {
    width: '90%'
  }
});
