import React from 'react';
import Typography from '@material-ui/core/Typography';

import BaseCard from '../BaseCard'
import src from './image.jpg'
import propTypes, { defaultProps } from './propTypes';

const title  = 'Bobcat Hire';

const BobcatHireCard = () => (
  <BaseCard title={title} image={{ title, src }}>
    <Typography>
      Bobcat and operator hire for clearing and excavation.
    </Typography>
  </BaseCard>
);

BobcatHireCard.propTypes = propTypes;
BobcatHireCard.defaultProps = defaultProps;

export default BobcatHireCard;
