import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';

import BobcatHireCard from './BobcatHireCard';
import CleanupCard from './CleanupCard';
import RemovalCard from './RemovalCard';

import propTypes, { defaultProps } from './propTypes';
import styles from './styles';

const ServicesSection = ({ classes }) => (
  <div className={classes.root}>
    <Typography variant="h2" color="inherit" className={classes.heading}>
      Services
    </Typography>
    <Grid container direction="row" justify="center" alignItems="center" spacing={16}>
      <Grid item >
        <CleanupCard />
      </Grid>
      <Grid item >
        <RemovalCard />
      </Grid>
      <Grid item >
        <BobcatHireCard />
      </Grid>
    </Grid>
  </div>
);

ServicesSection.propTypes = propTypes;
ServicesSection.defaultProps = defaultProps;

export default withStyles(styles)(ServicesSection);
